@import "../../assets/variables.scss";

section {
  padding: 0;
  border-radius: 12px;
  //border: 1px solid $yellow-color;
  font-weight: 500;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  font-size: 18px;
  font-family: "Roboto", "Segoe UI", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;

  .section_title {
    line-height: 2.3rem;
    font-family: "Montserrat", "Roboto", "Segoe UI", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    font-size: $font-size-label;
    font-weight: 700;
    margin-bottom: 1rem;
  }

  .section-content {
    margin: 2rem 0;
    padding: 20px 40px;
    background-color: #fffcf1;
    //border: 1px solid $blue-color;
    border-radius: 8px;
    width: 49%;
    color: $black-color;

    &_title {
      font-size: 1.4rem;
      font-weight: 700;
      color: #761924;
      margin: 1rem 0;
    }

    &_text {
    color: $secondary-text;
      font-size: 18px;
      font-weight: 400;
      line-height: 1.5;

      .briks{   width: 100%;
        padding: 0;
        letter-spacing: 0.1rem;
        font-weight: 500;
        li {
          margin-bottom: 6px;
        }
      }

      a {
        color: #141719;
        font-weight: 500;
        line-height: 1.3em;
      }
    }
  }
}

